.facts-section{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100vw;
    min-height: 100vh;
    margin-top: 10rem;
    // background-color: red;
    overflow: hidden;
}
.facts-section-inner{
    position: relative;
    width: 900px;
    max-width: 90vw;
    margin: auto;
    text-align: center;
    p{
        margin-top: 1.5rem;
        text-shadow: 1px 1px 5px black;
    }
}
.facts-section-text{
    margin-top: 4.5rem;
}
.facts-section-bg-wrapper{
    transform: scale3d(0.1,0.1,1);
    opacity: 0.5;
    position:absolute;
    height:100%;
    width:100%;
    left:0;
    top: 0;
    // background-image: url(../../assets/images/drums-bg.webp);
    // background-size: contain;
    img{
        width: 100%;
    }
}
